body{
    margin : auto;
    background : whitesmoke;
    color : rgb(32, 17, 116)
}

.first_div{
    height : 50px;
    width : 100%;
    background : rgb(209, 7, 7);
}

#logo{
    background : white;
    height: 40px;
    width : 40px;
    border-radius: 50%;
    text-align : center;
    font-size : 30px;
    font-weight: bold;
    margin-top : 5px;
    margin-left: 5%;
    color : red;
    float: left;
    text-decoration: none;
}

#buttons{
    float : right;
    margin-top: 15px;
    margin-left: 70%;
    margin-right: 40px;
    color : white;
    
}
 .button{
    background-color:rgb(209, 7, 7);;
    border : none;
    font-size: 18px;
    color : white;
 }

#filter_container{
    height : 500px;
    width : 15%;
    background : white;
    margin-top : 20px;
    margin-left : 5%;
    box-shadow: 0px 3px 15px grey;
    border :2px solid white;
    float : left;
}

#products_heading{
    font-size: 30px;
    font-weight : bold;
    margin-top:10px;
    margin-left : 5%;
}
#filter_heading{
    margin-left: 20px;
    font-size: 20px;
    margin-top:10px;
}
.filter_subheading{
    margin-left: 20px;
    font-size: 15px;
    color : grey;
    margin-top: 10px;

}
.rest_checkbox{
    margin-left : 20px;
    margin-top: 15px;
}

#rest_div{
    border : 2px solid black;
    height : 100%;

}
.restaurants{
    height : 250px;
    width : 60%;
    background  : white;
    margin-left:15%;
    margin-top : 20px;
    margin-bottom : 20px;
    border : 2px solid white;
    box-shadow: 0px 3px 15px grey;
    
}

.restaurants:hover{
    transform: scale(1.1,1.1);
    
}
.rest_img{
    
    margin-left : 20px;
    margin-top : 20px;
    float : left;
    
}
.rest_img img{
    border-radius : 20%;
    height :  100px;
    width:  110px;
}

.rest_heading{
    width : 45%;
    margin-left : 150px;
    margin-top : 20px;
}
.heading{
    font-size: large;
    font-weight : bold;
}

.subheading{
    font-weight : bold;
    color : grey;
}

.address{
    margin-bottom : 20px;

}
.cost{
    margin-top: 5%;
    margin-left:20px;
}
.hrline{
    color : black;
    background-color: black;
}

.pagination{
    display: inline-block;
    text-align: center;
    margin-left: 450px;
    
}
.pagination a{
    text-decoration: none;
    font-size: 30px;
    padding: 8px 16px;
    float: left;
    
}
.pagination a:hover{
    background-color: orange;
}
.active{
    background-color:rgb(243, 228, 14);
}
