}
.qdiv{
    margin: auto;
}
.tilecontainer{
    background: whitesmoke;
    height:150px;
    display: inline-block;
    margin: auto;
    margin-top: 30px;
    margin-bottom: 30px;
    margin-right: 3%;
    box-shadow: 0px 3px 15px black;
    
}
.imgstyle{
    height:150px;
    width:100%;   
}
.tilecomponent1{
    width: 46%;
    margin : auto;
    float: left;  
}
.tilecomponent2{
    height : 100%;
    width:46%;
    float: left;
    margin-top: 20px;
    margin-left:20px;
}
.heading{
    font-size: 150%;
    color:#192f60;
    font-weight: bold;
    
}
.subheading{
    color:gray;
    font-size: 120%;
    line-height: 1;
}
.tilecontainer:hover{
    
    transform: scale(1.1,1.1);
    
}